<svelte:head>
	<link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet">
</svelte:head>

<script>
	import Item from './Item.svelte';
	let items = [''];
	let raffleItems = [];
	let nextId = 2;
	let enteringData = true;

	function onRemove(e) {
		const idx = this.getAttribute('idx');
		items.splice(idx, 1);
		items = items;
	}
	function onCreated(node) {
		node.focus();
	}
	function onAdd(e) {
		items.push('');
		items = items;
		nextId += 1;
	}
	function onKeyUp(e) {
		if (e.key === 'Enter') {
			onAdd();
		}
	}
	import { tweened } from 'svelte/motion';
	import { bounceInOut, sineOut } from 'svelte/easing';
	function onStart(e) {
		console.log('items', items);
		raffleItems = [];
		raffleItems = items.map((val, idx) => { return {idx: idx, val: val};});

		let opc = Array(items.length);
		opc.fill(10000.0);
		console.log('opc', opc);
		opacities = tweened(opc);
		console.log('opc', opc);
		enteringData = false;
		winner = undefined;
		if (raffleItems.length > 60) {
			raffleFont = 12;
		}
		if (raffleItems.length > 40) {
			raffleFont = 24;
		}
		if (raffleItems.length > 20) {
			raffleFont = 36;
		}

		const interval = setInterval(() => {
			if (!makeAPick()) {
				clearInterval(interval);
			}
		}, 2000);

	}



	import { fade, fly, slide, scale, draw } from 'svelte/transition';
	const colors = [
		'#f44336',
		'#9C27B0',
		'#3F51B5',
		'#2196F3',
		'#009688',
		'#4CAF50',
		'#FF9800',
		'#795548',
		'#607D8B'
	];

	let raffleFont = 36;
	let winner;
	let opacities = tweened([]);

	function onEnterData(e) {
		enteringData = true;
		raffleItems = [];
	}

	function makeAPick() {
		if (raffleItems.length > 1) {
			const idx = Math.floor((Math.random() * raffleItems.length));
			console.log('pick', idx);
			const rem = raffleItems.splice(idx, 1);
			console.log('removed', rem);
			console.log('raff', raffleItems);
			console.log('opac', $opacities);
			opacities.update(o => {o[rem[0].idx] = 1000.0; console.log('inner opac', o); return o;});
			raffleItems = raffleItems;
			if (raffleItems.length > 1) {
				return true;
			}
		}
		winner = raffleItems[0].val;
		return false;
	}
	function color(idx) {
		return colors[idx % colors.length];
	}
</script>

<style>
	:global(body) {
		margin: auto 3rem;
	}
	ul {
		list-style-type: none;
		padding-inline-start: 0;
	}
	input {
		width: 250px;
	}
	.material-icons {
		color: #FFF;
	}
	.borderless .material-icons {
		font-size: 24px;
		vertical-align: middle;
	}
	.borderless {
		border: none;
		border-radius: 50%;
	}
	input + .borderless {
		margin-left: 12px;
		vertical-align: bottom;
	}
	.borderless.add {
		margin-top: 12px;
	}
	.regular {
		font-size: 24px;
		border-radius: 1em;
		padding: 0.4em 0.9em;
		margin-top: 20px;
	}
	.regular .material-icons {
		font-size: 26px;
		vertical-align: text-bottom;
	}
	button {
		background-color: #676778;
		color: #FFF;
		box-shadow: 0 3px 5px -1px rgba(0,0,0,.2), 0 6px 10px 0 rgba(0,0,0,.14), 0 1px 18px 0 rgba(0,0,0,.12);
	}


	.raffle-items {
		margin-top: 20px;
	}
	.raffle-item {
		color: #FFF;
		margin: 20px;
		display: inline-block;
		font-size: 36px;
		border-radius: 1em;
		padding: 0.4em 0.9em;
		box-shadow: 0 3px 5px -1px rgba(0,0,0,.2), 0 6px 10px 0 rgba(0,0,0,.14), 0 1px 18px 0 rgba(0,0,0,.12);
	}
	.winner {
		font-size: 36px;
		margin: 10px;
	}
</style>

<h1>Raffle</h1>

{#if enteringData}

	<ul>
		{#each items as item, i (i)}
			<li>
				<input bind:value={item} on:keyup={onKeyUp} use:onCreated style="color: {color(i)}"/>
				<button class="borderless" idx={i} on:click={onRemove}>
					<i class="material-icons">remove_circle_outline</i>
				</button>
			</li>
		{/each}

		<li>
			<button on:click={onAdd} class="borderless add">
				<i class="material-icons">add_circle_outline</i>
			</button>
		</li>
	</ul>


	<button class="regular" on:click={onStart}>
		<i class="material-icons">shuffle</i>
		Start
	</button>
{:else}
	<button class="regular" on:click={onEnterData}>
		<i class="material-icons">arrow_back</i>
		Back
	</button>

	<div class="winner">
		{#if winner}
			The winner is... <strong>{winner}</strong>
		{:else}
			&nbsp;
		{/if}
	</div>

	<div class="raffle-items">
		{#each items as item, i (i)}
			<div class="raffle-item" style="background-color: {color(i)}; opacity: { 1.0 * $opacities[i] / 10000.0};">
				{item}
			</div>
		{/each}
	</div>

{/if}
